import React from "react";
import { isChangeEvent } from "shared/helpers/dom";
import { toNumber } from "shared/helpers/number";
import { initialState } from "./constants";
import { calcChitState } from "./helpers";
import { E_ChitActions, T_ChitAction, T_ChitState } from "./types";

export function chitReducer(
  state: T_ChitState,
  [type, payload]: T_ChitAction
): T_ChitState {
  switch (toNumber(type)) {
    case E_ChitActions.clearEverything: {
      return initialState;
    }
    /**
     * Deposit actions
     */
    case E_ChitActions.depositAdd: {
      const newDeposits = [...state.deposits, { amount: 0, interestRate: 0 }];
      return calcChitState(
        state, 
        { deposits: newDeposits }
      );
    }
    case E_ChitActions.depositRemove: {
      const newDeposits = state.deposits.filter(
        (_d, index) => index !== payload?.index
      );
      return calcChitState(
        state, 
        { deposits: newDeposits }
      );
    }
    case E_ChitActions.depositAmountInput: {
      const newDeposits = state.deposits.map(
        (d, index) => {
          if (payload?.index === index) {
            return { ...d, amount: toNumber(payload.value) }
          }
          return d;
        }
      );
      return calcChitState(
        state, 
        { deposits: newDeposits }
      );
    }
    case E_ChitActions.depositInterestRateInput: {
      const newDeposits = state.deposits.map(
        (d, index) => {
          if (payload?.index === index) {
            return { ...d, interestRate: toNumber(payload.value) }
          }
          return d;
        }
      );
      return calcChitState(
        state, 
        { deposits: newDeposits }
      );
    }
    case E_ChitActions.chitTotalAmountInput: {
      const newChitInput = {
        ...state.chitInput,
        totalAmount: toNumber(payload?.value)
      };
      return calcChitState(
        state, 
        { chitInput: newChitInput }
      );
    }
    case E_ChitActions.chitDurationInput: {
      const newChitInput = {
        ...state.chitInput,
        duration: toNumber(payload?.value)
      };
      return calcChitState(
        state, 
        { chitInput: newChitInput }
      );
    }
    case E_ChitActions.chitLastPaymentAmountInput: {
      const newChitInput = {
        ...state.chitInput,
        lastPaymentAmount: toNumber(payload?.value)
      };
      return calcChitState(
        state, 
        { chitInput: newChitInput }
      );
    }
    case E_ChitActions.chitNoOfPaymentsSoFarInput: {
      const newChitInput = {
        ...state.chitInput,
        noOfPaymentsSoFar: toNumber(payload?.value)
      };
      return calcChitState(
        state, 
        { chitInput: newChitInput }
      );
    }
    case E_ChitActions.chitAgencyCommissionPercentInput: {
      const newChitInput = {
        ...state.chitInput,
        agencyCommissionPercent: toNumber(payload?.value)
      };
      return calcChitState(
        state, 
        { chitInput: newChitInput }
      );
    }
    case E_ChitActions.chitAuctionBidAmountInput: {
      const newChitInput = {
        ...state.chitInput,
        auctionBidAmount: toNumber(payload?.value)
      };
      return calcChitState(
        state, 
        { chitInput: newChitInput }
      );
    }
    default:
      return state;
  }
}

export function useEventHandler(
  dispatch: React.Dispatch<T_ChitAction>
) {
  const eventHandler = (
    event: React.ChangeEvent<HTMLInputElement> | React.MouseEvent<HTMLButtonElement>
  ) => {
    const value = isChangeEvent<HTMLInputElement>(event) ? event.target.value : "";
    const { action, index } = event.currentTarget.dataset;
    if (action) {
      const actionAndPayload: T_ChitAction = [
        action as unknown as E_ChitActions,
        { value, index: toNumber(index) }
      ];
      dispatch(actionAndPayload);
    }
  };

  return eventHandler;
}
