import { T_StringOrNumber } from "shared/types/common";
import { T_Nullable } from "shared/types/generics";

export enum E_ChitActions {
  clearEverything,
  chitTotalAmountInput,
  chitDurationInput,
  chitLastPaymentAmountInput,
  chitNoOfPaymentsSoFarInput,
  chitAgencyCommissionPercentInput,
  chitAuctionBidAmountInput,
  auctionInput,
  depositAdd,
  depositRemove,
  depositAmountInput,
  depositInterestRateInput
}

export type T_ChitAction = [
  type: E_ChitActions,
  payload?: { value?: T_StringOrNumber; index?: number; }
];

export type T_ChitInput = {
  totalAmount: T_Nullable<number>;
  duration: T_Nullable<number>;
  lastPaymentAmount: T_Nullable<number>;
  noOfPaymentsSoFar: T_Nullable<number>;
  agencyCommissionPercent: T_Nullable<number>;
  auctionBidAmount: T_Nullable<number>;
};

export type T_ChitStatus = {
  monthlyPaymentAmount: T_Nullable<number>;
  noOfMonthsRemaining: T_Nullable<number>;
  agencyCommissionAmount: T_Nullable<number>;
  normalChitTotalInHand: T_Nullable<number>;
  lastAuctionBidAmount: T_Nullable<number>;
}

export type T_Deposit = {
  amount: T_Nullable<number>;
  interestRate: T_Nullable<number>;
};

export type T_ChitAuctionResult = {
  auctionedChitTotalInHand: T_Nullable<number>;
  auctionedChitTotalInHandAndDeposits: T_Nullable<number>;
  diffAmountWithNormalChitTotalInHand: T_Nullable<number>;
};

export type T_ChitState = {
  chitInput: T_ChitInput;
  chitStatus: T_ChitStatus;
  deposits: T_Deposit[];
  depositInterestTotal: T_Nullable<number>;
  auctionResult: T_ChitAuctionResult;
};
