import { toNumber } from "shared/helpers/number";
import { T_ChitInput, T_ChitStatus, T_Deposit, T_ChitState } from "./types";

export function calcChitStatus(chitInput: T_ChitInput): T_ChitStatus {
  const duration = toNumber(chitInput.duration);

  let monthlyPaymentAmount = 0;
  if (duration > 0) {
    monthlyPaymentAmount = toNumber(chitInput.totalAmount) / duration;
  }

  const noOfMonthsRemaining = duration - toNumber(chitInput.noOfPaymentsSoFar);
  const agencyCommissionAmount =
    (toNumber(chitInput.totalAmount) *
      toNumber(chitInput.agencyCommissionPercent)) /
    100;
  const normalChitTotalInHand =
    toNumber(chitInput.totalAmount) - agencyCommissionAmount;

  let lastAuctionBidAmount = 0;
  if (toNumber(chitInput.lastPaymentAmount) > 0) {
    lastAuctionBidAmount =
      agencyCommissionAmount +
      (toNumber(monthlyPaymentAmount) - toNumber(chitInput.lastPaymentAmount)) *
        duration;
  }

  return {
    monthlyPaymentAmount,
    noOfMonthsRemaining,
    agencyCommissionAmount,
    normalChitTotalInHand,
    lastAuctionBidAmount
  };
}

export function calcDepositInterestTotal(
  deposits: T_Deposit[],
  duration: number
) {
  return deposits.reduce((total, deposit) => {
    const interestAmount =
      ((toNumber(deposit.amount) * (toNumber(deposit.interestRate) / 100)) /
        12) *
      duration; // no.of months in a year;

    return total + interestAmount;
  }, 0);
}

export function calcChitAuctionResult(
  chitInput: T_ChitInput,
  depositInterestTotal: number,
  chitStatus: T_ChitStatus
) {
  const auctionedChitTotalInHand =
    toNumber(chitStatus.normalChitTotalInHand) -
    (toNumber(chitInput.auctionBidAmount) -
      toNumber(chitStatus.agencyCommissionAmount));
  const auctionedChitTotalInHandAndDeposits =
    auctionedChitTotalInHand + depositInterestTotal;
  const diffAmountWithNormalChitTotalInHand =
    auctionedChitTotalInHandAndDeposits -
    toNumber(chitStatus.normalChitTotalInHand);

  return {
    auctionedChitTotalInHand,
    auctionedChitTotalInHandAndDeposits,
    diffAmountWithNormalChitTotalInHand
  };
}

export function calcChitState(
  prevState: T_ChitState,
  changedStateProps: Partial<T_ChitState>
): T_ChitState {
  let depositInterestTotal = prevState.depositInterestTotal;
  let chitStatus = prevState.chitStatus;
  let auctionResult = prevState.auctionResult;
  let deposits = prevState.deposits;
  let chitInput = prevState.chitInput;

  if (changedStateProps.deposits) {
    deposits = changedStateProps.deposits;
    depositInterestTotal = calcDepositInterestTotal(
      deposits,
      toNumber(prevState.chitStatus.noOfMonthsRemaining) - 1
    );
    auctionResult = calcChitAuctionResult(
      prevState.chitInput,
      depositInterestTotal,
      prevState.chitStatus
    );
  } else if (changedStateProps.chitInput) {
    chitInput = changedStateProps.chitInput;
    chitStatus = calcChitStatus(chitInput);
    if (toNumber(chitInput.auctionBidAmount) > 0) {
      auctionResult = calcChitAuctionResult(
        chitInput,
        toNumber(prevState.depositInterestTotal),
        chitStatus
      );
    }
  }

  return {
    depositInterestTotal,
    chitStatus,
    auctionResult,
    deposits,
    chitInput
  };
}
